@charset "UTF-8";

/* 폰트 */
@font-face {
    font-family: 'JMischievous';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/JGaegujaengyi-Light-KO.woff2') format('woff2');
    font-weight: 300;
    font-display: swap;
}

@font-face {
    font-family: 'Cafe24Decobox';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/2405-3@1.1/Cafe24Decobox.woff2') format('woff2');
    font-weight: normal;
    font-display: swap;
}

/* ========================= Root / Reset ========================= */
:root {
    --header-h: 8vh;
    --brand: #b19fff;
    --ink: #111;

    /* ▼ 스킬 칩 관련 변수(누락 보완) */
    --chip: 120px;
    --chip-radius: 10px;
    --icon: 70px;

    --proj-lines: 3;
    /* 기본 노출 줄 수 */
    --proj-lh: 1.2;
    /* .projBody의 line-height와 맞추기 */
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    list-style: none;
    font-family: 'JMischievous';
    color: #2a2a2a;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    /* 스크롤바 폭 고정으로 레이아웃 시프트 방지 */
    scrollbar-gutter: stable both-edges;
}

body {
    width: 100%;
    background:
        linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)),
        url("../public/img/bg.jpg") center/cover no-repeat;

    /* ✅ 고정 헤더 아래로 전체 콘텐츠 내리기 */
    padding-top: var(--header-h);
}

/* ========================= Header ========================= */
header {
    position: fixed;
    top: 0;
    /* ← 추가: 뷰포트 맨 위에 고정 */
    left: 0;
    /* ← 선택: 좌측 기준 고정(안전) */
    width: 100%;
    height: var(--header-h);
    /* 8vh 대신 변수 사용 권장 */
    background: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    z-index: 9999;
}

.headerBtn {
    font-size: 2rem;
    background: transparent;
    cursor: pointer;
    letter-spacing: .5px;
}

.headerBtn>p {
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
}

.headerNav {
    display: flex;
    gap: 100px;
}

.headerNav a {
    font-size: 2rem;
    font-weight: bold;
    text-decoration: none;
    letter-spacing: -2px;
}

/* ========================= Sections (common) ========================= */
#Main,
#Profile,
#Skills,
#Projects,
#Contact {
    width: 100%;
    height: 100vh;
    min-height: calc(100vh - var(--header-h));
    scroll-margin-top: var(--header-h);
}

/* ========================= Main ========================= */
#Main {
    width: 100%;
    margin: 0 auto;
}

/* ===== MainHero ===== */
#Main.hero {
    display: grid;
    place-items: center;
    /* background: #f5f5f5; */
    min-height: calc(100vh - var(--header-h));
    /* ✂️ 중복 방지: body에서 패딩 줬으니 제거
    padding-top: var(--header-h);
    */
}

.hero__inner {
    width: min(1440px, 100%);
    display: grid;
    grid-template-columns: 550px 1fr;
    align-items: center;
    gap: clamp(24px, 6vw, 64px);
}

/* Left image */
.hero__left {
    display: flex;
    justify-content: flex-end;
}

.hero__avatar {
    width: min(520px, 44vw);
    height: auto;
    object-fit: contain;
    filter: drop-shadow(0 20px 40px rgba(0, 0, 0, .12));
}

/* Right copy */
.hero__right {
    width: 100%;
    color: #111;
}

.hero__quote {
    font-size: 3.5rem;
    letter-spacing: .02em;
    line-height: 1.2;
}

.hero__quote--sub {
    margin-top: .35em;
    text-align: right;
}

.hero__quote b {
    font-weight: 700;
}

.qmark {
    opacity: .6;
    margin: 0 .12em;
}

.hero__kr1 {
    margin: clamp(14px, 2.6vw, 28px) 0 clamp(20px, 3vw, 36px);
    font-size: 2.2rem;
    line-height: 1.7;
    letter-spacing: .04em;
}

.hero__kr1 b {
    font-size: 2.5rem;
    font-weight: 800;
}

.hero__title {
    font-size: clamp(28px, 4.2vw, 52px);
    font-weight: 600;
    letter-spacing: .04em;
}

.hero__title b {
    font-weight: 900;
}

.hero__kr2 {
    margin: clamp(14px, 2.6vw, 28px) 0 clamp(20px, 3vw, 36px);
    font-size: 2.2rem;
    line-height: 1.7;
    letter-spacing: .04em;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.bottomLine {
    position: relative;
    display: inline-block;
    overflow: visible;
}

.bottomLine::after {
    content: "";
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 0;
    height: 30px;
    background-color: rgba(173, 96, 245, 0.1);
    transform: skewX(-20deg);
    transform-origin: left bottom right;
    animation: underlineGrow 3s ease forwards;
}

@keyframes underlineGrow {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

/* Responsive */
@media (max-width: 980px) {
    .hero__inner {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .hero__quote--sub {
        text-align: center;
    }

    .hero__right {
        align-items: center;
    }

    .hero__avatar {
        width: min(420px, 66vw);
    }
}

/* ========================= Profile ========================= */
#Profile {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: calc(var(--header-h) + 20px);
}

.ProfileLeft,
.ProfileRight {
    width: 50%;
    min-height: calc(100vh - var(--header-h));
}

.ProfileLeft {
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.P_imgBox {
    position: relative;
    display: flex;
    align-items: flex-end;
    gap: 20px;
    top: 28%;
    left: 50%;
    transform: translate(-30%, 0);
}

.P_img1,
.P_img2,
.P_img3 {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

.P_img1 {
    width: 350px;
    height: 450px;
    top: -210px;
    left: 170px;
    z-index: 2;
}

.P_img2 {
    width: 200px;
    height: 300px;
    transform: rotate(15.261deg);
    top: -230px;
    left: 400px;
}

.P_img3 {
    width: 300px;
    height: 400px;
    transform: rotate(-15.261deg);
    top: -240px;
    left: 50px;
}

.Profile_textBox {
    position: absolute;
    bottom: 25%;
    left: 50%;
    transform: translate(-40%, 0);
    width: 850px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding-left: 10px;
}

.Profile_text1 {
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: 2.8px;
}

.Profile_text2 {
    height: 40px;
    line-height: 1;
    font-size: 2rem;
    font-weight: 600;
    letter-spacing: 3px;
    overflow-wrap: anywhere;
}

.ProfileRight {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 60px;
    align-self: center;
}

.aboutBox,
.educationBox,
.trainingBox {
    position: relative;
    top: -8%;
    width: 83%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border: 1px solid #2a2a2a;
    border-radius: 7px;
    background: #F5F5F5;
}

.aboutTitle,
.educationTitle,
.trainingTitle {
    position: absolute;
    top: -35px;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    padding: 0 16px;
    height: 30px;
    line-height: 30px;
    font-weight: 700;
    font-size: 2.3rem;
    letter-spacing: 2px;
    color: #222;
    white-space: nowrap;
    z-index: 1;
}

.aboutTitle::before,
.educationTitle::before,
.trainingTitle::before {
    content: "";
    position: absolute;
    inset: 0;
    z-index: -1;
    background-color: rgba(173, 96, 245, 0.1);
    transform: skewX(-20deg);
    transform-origin: right bottom;
    border-radius: 6px;
    pointer-events: none;
}

.about,
.education,
.training {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 7px;
}

.about {
    height: 150px;
}

.education {
    height: 350px;
}

.training {
    height: 130px;
}

.about p,
.education p,
.training p {
    font-size: 1.5rem;
    font-weight: 500;
    letter-spacing: 3px;
}

.about p span,
.education span,
.training span {
    font-size: 1.7rem;
    font-weight: 600;
    letter-spacing: 2px;
}

/* ========================= Skills ========================= */
#Skills {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

.skillsWrap {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.skillsTitle {
    width: 250px;
    position: relative;
    display: inline-block;
    font-size: 4rem;
    letter-spacing: 3px;
}

.skillsTitle::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: .15em;
    /* 글자와 간격 */
    width: 0;
    /* 애니메이션 시작값 */
    height: .7em;
    /* 밑줄 두께 */
    background: rgba(173, 96, 245, .12);
    /* 톤 맞춤(원하면 var(--brand)로 바꿔도 됨) */
    transform: skewX(-20deg);
    transform-origin: left bottom;
    z-index: -1;
    /* 글자 뒤로 */
    animation: underlineGrow 1.8s ease forwards;
}

/* 프로젝트에 이미 있으면 재사용, 없으면 추가 */
@keyframes underlineGrow {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

.skillsCarousel {
    position: relative;
    z-index: 5;
}

.skillsTrack {
    position: relative;
    overflow-x: auto;
    overflow-y: visible;
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory;
    overscroll-behavior-x: contain;
    scrollbar-width: none;
    padding: 50px 250px;
}

.skillsTrack::-webkit-scrollbar {
    display: none;
}

.skillsList {
    display: flex;
    gap: 30px;
    align-items: center;
}

.skillChip {
    position: relative;
    z-index: 6;
    flex: 0 0 auto;
    width: var(--chip);
    height: var(--chip);
    border-radius: var(--chip-radius);
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    scroll-snap-align: center;
    transition: transform .18s ease, filter .18s ease, box-shadow .18s ease;
    cursor: pointer;
    user-select: none;
    transform-origin: center;
    writing-mode: horizontal-tb;
}

.skillChip img {
    width: var(--icon);
    height: var(--icon);
    object-fit: contain;
    pointer-events: none;
}

.skillChip span {
    display: block;
    margin-top: 8px;
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: .5px;
    color: #000;
    line-height: 1;
    white-space: nowrap;
    writing-mode: horizontal-tb;
    text-orientation: mixed;
    transition: color .25s ease, transform .25s ease;
}

.skillChip.is-active {
    box-shadow: 0 10px 30px rgba(0, 0, 0, .15);
}

.skillChip:not(.is-active) {
    filter: grayscale(.4) brightness(.9) opacity(.8);
}

.skillDetail {
    background: #fff;
    border-radius: 12px;
    padding: 30px 24px;
    box-shadow: 0 4px 12px rgba(0, 0, 0, .08);
}

.skillName {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 15px;
}

.skillDesc {
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 1.5;
}

.skillMeter {
    width: 100%;
    max-width: 1200px;
    margin: 26px auto 0;
}

.meterTrack {
    height: 20px;
    background: var(--ink);
    border-radius: 999px;
    overflow: hidden;
}

.meterFill {
    display: block;
    height: 100%;
    width: 0%;
    background: var(--brand);
    border-top-right-radius: 999px;
    border-bottom-right-radius: 999px;
    transition: width .7s cubic-bezier(.22, .9, .24, 1);
}

.meterScore {
    text-align: right;
    font-size: 1rem;
    margin-top: 6px;
    color: var(--ink);
}

.meterVal {
    font-weight: 700;
}

/* ========================= Projects ========================= */
#Projects,
#Project02,
#Project03,
#Project04,
#Project05 {
    width: 100%;
    min-height: calc(100vh - var(--header-h));
    display: flex;
    /* ⬇️ 중앙정렬 → 상단정렬 (세로 점프 방지) */
    align-items: flex-start;
    justify-content: center;
    padding: 30px 20px;
}

.projWrap {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    overflow-anchor: none;
}

.projHead {
    text-align: left;
    font-size: 4rem;
    letter-spacing: .2rem;
    margin-bottom: 30px;
}

.projGrid {
    display: grid;
    grid-template-columns: 1.3fr .9fr;
    /* ⬇️ center → start (컨텐츠 높이 변동 시 상단 기준) */
    align-items: start;
    gap: 40px;
}

.device {
    width: 100%;
    height: 600px;
    aspect-ratio: 15/10;
    border-radius: 18px;
    background: linear-gradient(#0b0b0b, #151515);
    padding: 18px 16px 24px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, .2);
    position: relative;
}

.device-topbar {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 110px;
    height: 14px;
    background: #0b0b0b;
    border-radius: 0 0 10px 10px;
    display: grid;
    place-items: center;
}

.device-topbar .cam {
    width: 6px;
    height: 6px;
    background: #0d1720;
    border-radius: 50%;
    box-shadow: 0 0 6px rgba(69, 170, 255, .35) inset;
}

.screen {
    width: 100%;
    height: 100%;
    border-radius: 12px;
    overflow: hidden;
    background: #fff;
}

/* ⬇️ 이미지 비율 고정으로 CLS 방지 */
.screen img,
.screen iframe,
.screen video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 0;
    display: block;
    aspect-ratio: 15 / 10;
}

.device-foot {
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    height: 10px;
    border-radius: 0 0 12px 12px;
    background: linear-gradient(#777, #333);
    filter: blur(.6px);
}

/* 카드: 자동으로 내용에 맞게 커지도록 (음수 마진 제거) */
.projCard {
    width: 450px;
    min-height: 600px;
    height: auto;
    background: #fff;
    border-radius: 14px;
    padding: 22px 24px;
    box-shadow: 7px 7px 5px rgba(0, 0, 0, 0.05);
    /* ⬇️ 점프 유발하던 음수 마진 제거 */
    /* margin-top: -50px;  */
    margin-left: auto;
    /* ⬇️ 레이어 승격으로 전환 부드럽게 */
    transform: translateZ(0);
    overflow-anchor: none;
}

.projTitle {
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 20px;
    text-align: center;
}

.pageBtns {
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 8px;
}

.pageBtns a {
    text-decoration: none;
    padding: 6px 14px;
    border-radius: 6px;
    background: #f5f5f5;
    color: #333;
    border: 1px solid #ddd;
    font-size: 0.9rem;
    transition: 0.2s ease;
}

.pageBtns a:hover {
    background: #5E472F;
    color: #fff;
    border-color: #5E472F;
}

/* 본문: 접기/펼치기 */
.projBody {
    font-size: 1.5rem;
    line-height: var(--proj-lh);
    letter-spacing: .02rem;
    text-align: left;
    margin-bottom: 16px;
    position: relative;
    overflow: hidden;
    max-height: calc(var(--proj-lines) * var(--proj-lh) * 1em);
    /* ⬇️ 전환 최적화 */
    transition: max-height .45s ease;
    will-change: max-height;
}

.projBody::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 3rem;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0), #fff);
    pointer-events: none;
}

/* 열림 상태 */
.projBody.is-open {
    max-height: 1000px;
}

.projBody.is-open::after {
    opacity: 0;
}

/* 정보/기술/버튼 영역 */
.projInfo {
    display: grid;
    gap: 6px;
    margin: 10px 0 30px;
    font-size: 1.5rem;
}

.projInfo b {
    font-weight: 800;
}

.projTech {
    display: flex;
    gap: 12px;
    align-items: center;
    margin-bottom: 30px;
}

.projTech img {
    width: 32px;
    height: 32px;
    object-fit: contain;
}

.projBtns {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
    margin-top: 20px;
}

/* 더보기/접기 버튼 (토글 전용) */
.projToggle {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 7px 10px;
    border-radius: 10px;
    background: var(--brand);
    color: #fff;
    font-size: 1.1rem;
    font-weight: 700;
    letter-spacing: 1px;
    text-decoration: none;
    cursor: pointer;
    border: 0;
    transition: transform .2s ease, opacity .2s ease;
}

.projToggle:hover {
    transform: translateY(-1px);
}

/* 부드럽게 닫히는 info/tech 전환 */
.projExtra {
    overflow: hidden;
    max-height: 500px;
    opacity: 1;
    transform: translateY(0);
    transition: max-height .5s ease, opacity .35s ease, transform .5s ease;
    will-change: max-height, opacity, transform;
}

.projExtra.hide {
    max-height: 0;
    opacity: 0;
    transform: translateY(-10px);
    pointer-events: none;
}

/* 접근성: 모션 최소화 설정 시 전환 제거 */
@media (prefers-reduced-motion: reduce) {

    .projBody,
    .projExtra {
        transition: none !important;
    }
}

/* Project03(Phone) */
#Project03 {
    width: 100%;
    min-height: calc(100vh - var(--header-h));
    display: flex;
    /* ⬇️ 상단 정렬 */
    align-items: flex-start;
    justify-content: center;
    padding: 60px 20px;
}

.device-phone {
    position: relative;
    width: 393px;
    min-height: 650px;
    height: auto;
    border-radius: 40px;
    background: linear-gradient(145deg, #111, #222);
    padding: 18px;
    box-shadow: 0 12px 30px rgba(0, 0, 0, .25);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.phone-notch {
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translateX(-50%);
    width: 130px;
    height: 22px;
    background: #000;
    border-radius: 10px;
}

.phone-screen {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 30px;
    overflow: hidden;
    background: #fff;
    margin-top: 22px;
}

.phone-screen img,
.phone-screen iframe {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: none;
    aspect-ratio: 393 / 650;
}

.phone-btn {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 4px;
    background: #333;
    border-radius: 3px;
}

#Project03 .projCard {
    width: 500px;
    min-height: 650px;
    height: auto;
    background: #fff;
    box-shadow: 7px 7px 5px rgba(0, 0, 0, 0.05);
}

#Project03 .projCard b {
    color: #000;
}

/* ========================= Contact ========================= */
#Contact {
    width: 100%;
    min-height: calc(100vh - var(--header-h));
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 20px;
}

.contactWrap {
    width: 100%;
    max-width: 1100px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1.2fr;
    gap: 40px;
    align-items: center;
}

.contactImg {
    width: 460px;
    max-width: 100%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background: #2a2a2a;
    box-shadow: 0 8px 24px rgba(0, 0, 0, .18) inset, 0 8px 24px rgba(0, 0, 0, .06);
    overflow: hidden;
    justify-self: center;
}

.contactAvatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.contactText {
    display: flex;
    flex-direction: column;
    gap: 22px;
}

.contactTitle {
    font-size: 4rem;
    letter-spacing: .08em;
    font-weight: 800;
}

.contactList {
    display: grid;
    gap: 12px;
}

.contactList li {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 3rem;
}

.contactList a {
    color: #2a2a2a;
    text-decoration: none;
}

.contactList a:hover {
    text-decoration: underline;
}

.ic {
    width: 22px;
    height: 22px;
    display: inline-block;
    position: relative;
    filter: drop-shadow(0 1px 1px rgba(0, 0, 0, .12));
}

.ic.mail::before {
    content: "";
    position: absolute;
    inset: 3px 2px 6px 2px;
    border: 2px solid #2a2a2a;
    border-top: none;
    border-radius: 3px;
}

.ic.mail::after {
    content: "";
    position: absolute;
    left: 3px;
    right: 3px;
    top: 7px;
    height: 0;
    border-top: 2px solid #2a2a2a;
    transform: skewY(-20deg);
    box-shadow: inset 0 0 0 2px #2a2a2a;
}

.ic.phone::before {
    content: "";
    position: absolute;
    inset: 1px;
    border-radius: 6px;
    border: 2px solid #2a2a2a;
}

.ic.phone::after {
    content: "";
    position: absolute;
    left: 7px;
    top: 3px;
    width: 8px;
    height: 16px;
    border-left: 2px solid #2a2a2a;
    border-right: 2px solid #2a2a2a;
    opacity: .001;
}

.ic.insta {
    background: radial-gradient(circle at 70% 30%, #2a2a2a 2px, transparent 3px) no-repeat, conic-gradient(#2a2a2a 0 360deg);
    -webkit-mask: radial-gradient(circle 6px at 50% 50%, #000 98%, transparent 100%) padding-box, none;
    mask: radial-gradient(circle 6px at 50% 50%, #000 98%, transparent 100%) padding-box, none;
    border-radius: 6px;
}

.contactMsg {
    font-size: 1.5rem;
    margin-top: 6px;
    color: #333;
    line-height: 1.7;
    letter-spacing: .02rem;
}

/* ========================= Responsive (global) ========================= */
@media (max-width:980px) {
    .projGrid {
        grid-template-columns: 1fr;
        gap: 26px;
    }

    .projHead {
        font-size: 2.2rem;
    }
}

/* ========================= Right bullets (Projects quick nav) ========================= */
.rb-nav {
    position: fixed;
    right: 22px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 9998;
    padding: 8px 10px;
    border-radius: 18px;
    backdrop-filter: blur(6px);
}

.rb-nav ul {
    display: grid;
    gap: 10px;
}

.rb-dot {
    position: relative;
    width: 20px;
    height: 20px;
    border-radius: 999px;
    background: #e6e6e6;
    border: 0;
    display: grid;
    place-items: center;
    cursor: pointer;
    transition: transform .15s ease, background .15s ease;
}

.rb-dot__inner {
    width: 10px;
    height: 10px;
    border-radius: 999px;
    background: #777;
    transition: background .15s ease, transform .15s ease;
}

.rb-tooltip {
    position: absolute;
    right: 150%;
    top: 50%;
    transform: translateY(-50%) scale(0.8);
    background: rgba(0, 0, 0, .8);
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 1.5px;
    padding: 4px 8px;
    border-radius: 6px;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: all .2s ease;
}

.rb-dot:hover .rb-tooltip {
    opacity: 1;
    transform: translateY(-50%) scale(1);
}

.rb-dot.is-active {
    background: var(--brand);
    box-shadow: 0 0 0 4px rgba(177, 159, 255, .18);
}

.rb-dot.is-active .rb-dot__inner {
    background: #fff;
    transform: scale(1.15);
}

.rb-dot.is-active .rb-tooltip {
    background: var(--brand);
}

@media (max-width:780px) {
    .rb-nav {
        right: 10px;
    }
}

@media (max-width:540px) {
    .rb-nav {
        right: 8px;
        padding: 6px 8px;
    }

    .rb-dot {
        width: 12px;
        height: 12px;
    }

    .rb-dot__inner {
        width: 5px;
        height: 5px;
    }
}

/* ===== Cursor hint (projects only) ===== */
.cursor-hint {
    position: fixed;
    left: 0;
    top: 0;
    transform: translate(-9999px, -9999px);
    z-index: 9999;
    pointer-events: none;
    padding: 8px 12px;
    font-size: 17px;
    letter-spacing: .4px;
    color: #111;
    background: #ffffffcc;
    border: 1px solid #e6e6e6;
    border-radius: 10px;
    box-shadow: 0 6px 16px rgba(0, 0, 0, .12);
    backdrop-filter: blur(6px);
    opacity: 0;
    transition: opacity .2s ease;
}

.cursor-hint.is-show {
    opacity: 1;
}

.cursor-hint::after {
    content: "";
    position: absolute;
    left: 6px;
    top: -6px;
    border: 6px solid transparent;
    border-bottom-color: #ffffffcc;
    filter: drop-shadow(0 -1px 0 #e6e6e6);
}

/* ========================= Image auto pan on hover ========================= */
/* 세로로 부드럽게 팬(위 -> 아래) */
.screen .panY,
.phone-screen .panY {
    object-fit: cover;
    object-position: 50% 0%;
    transition: object-position 5s linear;
    will-change: object-position;
}

.screen:hover .panY,
.phone-screen:hover .panY {
    object-position: 50% 100%;
}

/* 가로로 부드럽게 팬(왼 -> 오른) */
.screen .panX,
.phone-screen .panX {
    object-fit: cover;
    object-position: 0% 50%;
    transition: object-position 6s linear;
    will-change: object-position;
}

.screen:hover .panX,
.phone-screen:hover .panX {
    object-position: 100% 50%;
}

/* 모션 최소화 */
@media (prefers-reduced-motion: reduce) {

    .screen .panY,
    .screen .panX,
    .phone-screen .panY,
    .phone-screen .panX {
        transition: none !important;
    }
}